.batiq {
    border-radius: 10px;
    color: #7d7d80;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    background: antiquewhite;
}

.apy {
    position: relative;
    .percent {
        position: absolute;
        top: 54px;
        right: 17px;
    }
}

.padd-0 {
    padding: 0 !important;
}
.padd-l {
    padding-left: 0 !important;
}
.custom-control-input:checked ~ .custom-control-label::before {
    background-color: white;
}

.custom-control-input:checked ~ .custom-control-label::before {
    color: #000;
    border-color: grey;
    background-color: #ffff;
}
.custom-switch .custom-control-input:checked ~ .custom-control-label::after {
    background-color: #f6c325 !important;
    -webkit-transform: translateX(0.75rem);
    transform: translateX(0.75rem);
}
.switch .custom-switch .custom-control-label::before {
    width: 40px;
    height: 22px;
    top: 1px !important;
    border-radius: 100px;
}
.switch .custom-switch .custom-control-label::after {
    width: 17px;
    height: 17px;
    top: 3px !important;
}
.switch .custom-switch .custom-control-input:checked ~ .custom-control-label::after {
    transform: translateX(1.1rem);
}
.switch .custom-control-input:checked ~ .custom-control-label::before {
    border-color: #f6c325 !important;
}
.custom-control-label {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: #0b0e34;
    padding-left: 20px;
}

@media (max-width: 991px) {
    .padd-l {
        padding: 0 !important;
    }
}
@media (max-width: 600px) {
    .edit .pool-detail .input-field input {
        width: 100%;
    }
}
@media (max-width: 430px) {
    .edit .staking-token .input-field input {
        width: 100%;
    }
    .edit .reward-token .input-field input {
        width: 100%;
    }
    .edit .staking-token .input-field .upload {
        width: 100%;
    }
    .edit .reward-token .input-field .upload {
        width: 100%;
    }
    .edit .switch .input-field input {
        width: 100% !important;
    }
}
@media (max-width: 380px) {
    .edit .btn-create .btn-clear {
        margin: 0 !important;
    }
    .edit .btn-create button {
        margin-bottom: 10px;
    }
}
